* {
  box-sizing: border-box;
}

.overlay {
  background-color: rgba(186, 147, 147, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.auth-form-container {
  color: white;
  text-align: center;
  display: flex;
  top: 40%;
  left: 50%;
  min-height: 30vh;
  width: 400px;
  max-height: 500px;
  padding: 2rem;
  border-radius: 10px;
  margin: 10px;
  justify-content: center;
  background-image: linear-gradient(79deg, #333136, #888588 48%, #24080f);
  box-shadow: 0 0 20px 0 black;
}

.auth-form-container,
.register-form,
.login-form {
  display: flex;
  flex-direction: column;
}

label {
  text-align: left;
  padding: 0.25rem 0;
}

input {
  margin: 0.5rem 0;
  padding: 1rem;
  border: none;
  border-radius: 10px;
}

input:focus{
  outline: none;
}

button {
  border: none;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
}

.btn {
  font-weight: bold;
  color: rgb(50, 47, 47);
}

.link-btn {
  background: none;
  color: white;
  text-decoration: underline;
}

.error {
  margin-bottom: 10px;
  width: 300px;
  border-radius: 7px;
  font-size: 12px;
  background-color: rgb(208, 92, 92);
  padding: 2px;
}

.hidden {
  visibility: hidden
}

.welcome-screen {
  display: flex;
  flex-direction: column;
}



