.slider {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  overflow: hidden;
  background-image: linear-gradient(79deg, #6e637f7c, #88858881 48%, #b2445f7d);
}

.slide {
  display: flex;
  justify-content: center;
  opacity: 0;
  transition-duration: 1s easy;
}

.slide.active {
  opacity: 1;
  width: 100%;
  transition-duration: 1s;
  transform: scale(1.08);
}

.image {
  height: 92vh;
  border-radius: 15px;
}

.right-arrow {
  position: absolute;
  top: 50%;
  right: 7%;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 7%;
  font-size: 3rem;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.carousel__text {
  z-index: 200;
  position: absolute;
  max-width: 90%;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  position: fixed;
  font-weight: bold;
  color: white;
}

