.header {
  height: 8vh;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: end;
  
  background-image: linear-gradient(79deg, #6e637f, #888588 48%, #b2445f);
  
  /* border: 2px solid black; */
}

.header__btn {
  width: 100px;
  height: 60%;
  padding: 0;
  margin-right: 20px;

  font-weight: bold;

  border: 2px solid rgb(53, 53, 53);
  background-color: rgba(243, 238, 238, 0.872);
}

.header__btn:hover {
  background-color: rgba(243, 238, 238, 0.655);
  transition: all 0.8s;
}

.header__welcome-user {
  display: flex;
  margin-right: auto;
  margin-left: 30px;
  color: antiquewhite;
  font-weight: bold;
}

.display-none {
  display: none;
}

.long {
  width: 150px;
}
